import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import { css } from "@emotion/react"
import Layout from "../../components/layout"
import ArticleLink from "../../components/article-link"
import Seo from "../../components/seo"
import "../../styles/main-archives.css"

export default function Home({ data }) {
  return (
    <Layout>
      <Seo title="コラム" />

      <div className="wrap">
        <section className="archives">
          <h2>コラム</h2>
          <div className="archives-inner">
            <div className="flex">
              <div className="flex-item">
                <figure>
                  <StaticImage
                    src="../../images/main-column/editors-note-20211002/ph1.jpg"
                    alt="株式会社ホットソウルのホームページリニューアルしました！"
                    placeholder="blurred"
                  />
                </figure>
                <p className="date">
                  2021.10.2
                </p>
                <p className="title">
                  【HPリニューアル編集後記】
                  <br/>
                  小宮&nbsp;雅哲&nbsp;&nbsp;さん
                </p>
                <p className="txt">
                  株式会社ホットソウルのホームページリニューアルしました！ 
                </p>
                <div className="archives-btn">
                  <Link to={`editors-note`}>記事を読む</Link>
                </div>
              </div>
              {data.allContentfulColumn.edges.map(edge =>
                <ArticleLink key={edge.node.slug} article={edge.node} />
              )}
            </div>
          </div>
        </section>
      </div>

    </Layout>
  )
}


export const query = graphql`
  query allContentfulPost {
    allContentfulColumn {
      edges {
        node {
          id
          image1 {
            gatsbyImageData(placeholder: BLURRED)
          }
          updatedAt(formatString: "YYYY.M.D")
          title
          slug
          outline
          authorName
        }
      }
    }
  }
`

